.levelDiv {
  margin-top: 20px;
  margin-bottom: 20px;
  width: 95%;
  margin-left: 2.5%;
  font-size: 20px;
  border: 1px solid lightgray;
  padding: 2px 2px 2px 10px;
}

.levelItemCustom {
  /* border: 2px solid red; */
  width: auto;
}

.level {
  display: -ms-flexbox;
  display: flex;
  border-radius: 5px;
}

.is-scrollable {
  overflow: auto;
  position: absolute;
  top: 100px;
  bottom: 70px;
  width: 100%;
}

.is-positive {
  background-color: #c7f6c7;
  font-weight: bold;
}

.positiveLabel {
  background-color: #c7f6c7;
  font-weight: bold;
}

.is-notSure {
  background-color: palegoldenrod;
  font-weight: bold;
}

.is-negative {
  background-color: lightpink;
  font-weight: bold;
}

.notSet {
  background-color: #f1f1f1;
  font-weight: bold;
}

.listDiv {
  background-color: #f6fbfc;
}

.levelDiv input {
  position: absolute;
  top: 1000;
  right: -1000;
  -webkit-appearance: none;
  -moz-appearance: none;
  -ms-appearance: none;
  -o-appearance: none;
  appearance: none;
}

.levelDiv input[type="radio"]:checked+label {
  border: 2px solid black;
}


