.sortContainer {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  margin: 5px;
  /* justify-content: flex-start; */
  /* align-items: flex-start; */
  background: rgba(0, 0, 0, 0.1);
  min-height: 400px;
  max-height: 800px;
  overflow: auto;
}

.rootDiv {
  background-color: steelblue;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  padding: 0px, 10px, 10px, 10px;
  min-height: 100vh;
  /* flexbox */
  display: -ms-flexbox;
  display: flex;
  /* justify-content: center; */
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: start;
      align-items: flex-start;
}

.headersContainer {
  display: -ms-flexbox;
  display: flex;
  position: fixed;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: start;
      align-items: flex-start;
  height: 50px;
  -ms-flex-direction: "row-reverse";
      flex-direction: "row-reverse";
}

.lightgreen {
  background: lightgreen;
}

.sortFooter {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  background: #e4e4e4;
  position: fixed;
  left: 0px;
  bottom: 0;
  width: 100%;
  min-height: 150px;
}

.footerMessageBox {
  -ms-flex-preferred-size: 300px;
      flex-basis: 300px;
  width: 300px;
  border: 2px solid lightgray;
}

.footerMessageBox p {
  padding: 10px;
  margin-top: 20px;
  font-size: 25px;
  color: #3273dc;
}

.sortEndButton {
  position: fixed;
  bottom: 0;
  right: 0;
  margin: 10px;
}

.cardSlider {
  width: 1300px;
  overflow: hidden;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}



